import React from 'react';
import { useDispatch } from 'react-redux';

import { dataAutentifikasi } from '../redux/actions/myactions';

const Home = () => {
	const dispatch = useDispatch();

	React.useEffect(() => {
		dispatch(dataAutentifikasi());
	}, []);

	return (
		<React.Fragment>
		</React.Fragment>
	)
};

export default Home;