import React from 'react';
import { Link, useLocation } from 'react-router-dom';

const Header = () => {
  const location = useLocation();


  // location.pathname.slice(1)

	return (
		<React.Fragment>
			<div className="header bg-primary pb-6">
      <div className="container-fluid">
        <div className="header-body">
          <div className="row align-items-center py-4">
            <div className="col-lg-6 col-7">
              <h6 className="h2 text-white d-inline-block mb-0">Mata Kuliah</h6>
              <nav aria-label="breadcrumb" className="d-none d-md-inline-block ml-md-4">
                <ol className="breadcrumb breadcrumb-links breadcrumb-dark">
                  <li className="breadcrumb-item"><Link to="/"><i className="fas fa-home"></i></Link></li>
                  <li className="breadcrumb-item"><Link to={location.pathname} >
                  { location.pathname === '/web' && 'PEMBROGRAMAN BERBASIS WEB'  }
                  { location.pathname === '/kb' && 'KECERDASAN BUATAN'  }
                  { location.pathname === '/md' && 'MATEMATIKA DISKRIT'  }

                  </Link></li>
                </ol>
              </nav>
            </div>
            
          </div>
          {/* Card stats */}
          <div className="row">
            <div className="col-xl-4 col-md-6">
              <div className="card card-stats">
                {/* Card body */}
                <div className="card-body">
                  <div className="row">
                    <div className="col">
                      <h5 className="card-title text-uppercase text-muted mb-0">Mata Kuliah</h5>
                      <span className="h2 font-weight-bold mb-0">Kecerdasan Buatan</span>
                    </div>
                    <div className="col-auto">
                      <div className="icon icon-shape bg-gradient-red text-white rounded-circle shadow">
                        <i className="ni ni-active-40"></i>
                      </div>
                    </div>
                  </div>
                  <p className="mt-3 mb-0 text-sm">
                    <span className="text-success mr-2"><i className="fa fa-arrow-right"></i></span>
                    <Link to="/kb">
                    <button type="button" className="btn btn-primary btn-sm">Lihat</button>
                    </Link>
                  </p>
                </div>
              </div>
            </div>
            <div className="col-xl-4 col-md-6">
              <div className="card card-stats">
                {/* Card body */}
                <div className="card-body">
                  <div className="row">
                    <div className="col">
                      <h5 className="card-title text-uppercase text-muted mb-0">Mata Kuliah</h5>
                      <span className="h5 font-weight-bold mb-0">Pemrograman Berbasis WEB</span>
                    </div>
                    <div className="col-auto">
                      <div className="icon icon-shape bg-gradient-orange text-white rounded-circle shadow">
                        <i className="ni ni-chart-pie-35"></i>
                      </div>
                    </div>
                  </div>
                  <p className="mt-3 mb-0 text-sm">
                    <span className="text-success mr-2"><i className="fa fa-arrow-right"></i></span>
                    <Link to="/web">
                    <button type="button" className="btn btn-primary btn-sm">Lihat</button>
                    </Link>
                  </p>
                </div>
              </div>
            </div>
            <div className="col-xl-4 col-md-6">
              <div className="card card-stats">
                {/* Card body */}
                <div className="card-body">
                  <div className="row">
                    <div className="col">
                      <h5 className="card-title text-uppercase text-muted mb-0">Mata Kuliah</h5>
                      <span className="h2 font-weight-bold mb-0">Matematika Diskrit</span>
                    </div>
                    <div className="col-auto">
                      <div className="icon icon-shape bg-gradient-green text-white rounded-circle shadow">
                        <i className="ni ni-money-coins"></i>
                      </div>
                    </div>
                  </div>
                  <p className="mt-3 mb-0 text-sm">
                    <span className="text-success mr-2"><i className="fa fa-arrow-right"></i></span>
                    <Link to="/md">
                    <button type="button" className="btn btn-primary btn-sm">Lihat</button>
                    </Link>
                  </p>
                </div>
              </div>
            </div>
           
          </div>
        </div>
      </div>
    </div>
		</React.Fragment>
	)
}

export default Header;