export const ubahDataPrediksi = (mydata) => {
	return async (dispatch, getState) => {
		dispatch({
			type: actionTypes.PROSES_AMBIL_DATA
		});
		Db.inputanApriori(mydata)
		.then(response => {
			dispatch({
				type: actionTypes.FETCH_DATA_PREDIKSI,
				payload: {datanya: response.data, ket: 'prediksi'},
			});
		})
		.catch(e => {
			dispatch({
				type: actionTypes.GAGAL_AKSES,
			});
		});

		dispatch({
			type: actionTypes.PROSES_AMBIL_DATA
		});

		Db.listApriori()
		.then(response => {
			dispatch({
				type: actionTypes.FETCH_DATA_PREDIKSI,
				payload: {datanya: response.data, ket: 'prediksi'},
			});
		})
		.catch(e => {
			dispatch({
				type: actionTypes.GAGAL_AKSES,
			});
		});



	};
}

