import { combineReducers } from 'redux';
import { dataAutentifikasi, dataku, dataPrediksi, naikanAngka, mydata, dataAnggota, detailAnggota, detailArsip } from './myreducers';

const semuaReducers = combineReducers({
	counter: dataku,
	dataPrediksi: dataPrediksi,
	naikanAngka: naikanAngka,
	mydata: mydata,
	dataAnggota: dataAnggota,
	detailAnggota: detailAnggota,
	detailArsip: detailArsip,
	dataAutentifikasi: dataAutentifikasi,
});

export default semuaReducers;