import React from 'react';
// import $ from 'jquery';
import team from '../img/team.jpg';

import { useSelector } from 'react-redux';

// turunan dari navbar
import NavbarCollapse from './NavbarCollapse';
// import NavbarCollapsePesan from './NavbarCollapsePesan';
// import NavbarCollapseKotak from './NavbarCollapseKotak';

// menggunakan reducer
const initialState = {
  tampilSatu: false,
  tampilDua: false,
  tampilTiga: false,
  pilihan: null
};

const actionTypes = {
  PILIHAN_SATU: 'PILIHAN_SATU',
  PILIHAN_DUA: 'PILIHAN_DUA',
  PILIHAN_TIGA: 'PILIHAN_TIGA',
}


const reducer = (state, action) => {
  switch(action.type) {
    case actionTypes.PILIHAN_SATU:
      return {
        ...state,
        tampilSatu: true,
        tampilDua: false,
        tampilTiga: false,
        pilihan: 1
      }
    case actionTypes.PILIHAN_DUA:
      return {
        ...state,
        tampilSatu: false,
        tampilDua: true,
        tampilTiga: false,
        pilihan: 2,
      }
    case actionTypes.PILIHAN_TIGA:
      return {
        ...state,
        tampilSatu: false,
        tampilDua: false,
        tampilTiga: true,
        pilihan: 3,
      }
    case actionTypes.PILIHAN_HIDE:
      return {
        ...state,
        tampilSatu: false,
        tampilDua: false,
        tampilTiga: false,
        pilihan: null,
      }
    default:
      return state
  }
}

const Navbar = () => {
  // const [tampil, setTampil] = React.useState(false);
  // const [show, setShow] = React.useState('');
  // const [pilihannya, setPilihannya] = React.useState(0);
  const [state, dispatch] = React.useReducer(reducer, initialState);
  // const [styleSearch, setStyleSearch] = React.useState("form-group mb-0");

React.useEffect(()=>{

// ===========jquey--------


// ===========end jquery =====---

}, [])

const actionMemilih = () => {
  return {
    type: actionTypes.PILIHAN_SATU,
  }
}

const actionHide = () => {
  return {
    type: actionTypes.PILIHAN_HIDE,
  }
}

// const actionPilihDua = () => {
//   return {
//     type: actionTypes.PILIHAN_DUA,
//   }
// }

// const actionPilihTiga = () => {
//   return {
//     type: actionTypes.PILIHAN_TIGA
//   }
// }


const handleKlik = (e) => {
  e.preventDefault();
  if (state.tampilSatu) {
    console.log('true');
    dispatch(actionHide());
  } else {
    console.log('false');
    dispatch(actionMemilih());
  }
};

// const handleKlikDua = (e) => {
//   e.preventDefault();
//   if (state.tampilDua) {
//     dispatch(actionHide());
//   } else {
//     dispatch(actionPilihDua());
//   }
// };

// const handleKlikTiga = (e) => {
//   e.preventDefault();
//   if (state.tampilTiga) {
//     dispatch(actionHide());
//   } else {
//     dispatch(actionPilihTiga());
//   }
// }


  const datanya = useSelector(state => state.semuaReducers.dataAutentifikasi.itemdetail);

	return (
		<React.Fragment>
			<nav className="navbar navbar-top navbar-expand navbar-dark bg-primary border-bottom">
      <div className="container-fluid">
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          
          <ul className="navbar-nav align-items-center ml-md-auto">
            
           <li className="nav-item d-xl-none">
              <div className="pr-3 sidenav-toggler sidenav-toggler-dark" data-action="sidenav-pin" data-target="#sidenav-main">
                <div className="sidenav-toggler-inner">
                  <i className="sidenav-toggler-line"></i>
                  <i className="sidenav-toggler-line"></i>
                  <i className="sidenav-toggler-line"></i>
                </div>
              </div>
            </li>
            
            
          </ul>
          <ul className="navbar-nav align-items-center ml-auto ml-md-0">
            <li className={state.tampilSatu ? '"nav-item dropdown show"' : '"nav-item dropdown' }>
              <a onClick={(e) => handleKlik(e)} className="nav-link pr-0" href="/#" rel="noopener noreferrer" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded={state.tampilSatu}>
                <div className="media align-items-center">
                  <span className="avatar avatar-sm rounded-circle">
                    <i className="ni ni-single-02"></i>
                  </span>
                  <div className="media-body ml-2 d-none d-lg-block">
                    <span className="mb-0 text-sm  font-weight-bold">{ datanya.length > 0 ? datanya.akun : 'belum login' }</span>
                  </div>
                </div>
              </a>
              { state.tampilSatu ? <NavbarCollapse pilihan={state.pilihan} /> : null }

              
            </li>
          </ul>
        </div>
      </div>
    </nav>
		</React.Fragment>
	)
};

export default Navbar;