import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Switch, Route } from 'react-router-dom';

// import Dashboard from './component/Dashboard';
// import DataAnggota from './component/DataAnggota';
import Nav from './component/Nav';
import Navbar from './component/Navbar';
import Header from './component/Header';
import KecerdasanBuatan from './component/KecerdasanBuatan';
import Web from './component/Web';
import MatematikaDiskrit from './component/MatematikaDiskrit';
import Profile from './component/Profile';
import Home from './component/Home';

import { createStore, applyMiddleware, compose } from 'redux';
import allReducers from './reducers/index';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';


// untuk meggabungkan devtools dengan applyMiddleware
const composeEnhancers = typeof window == 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({}) : compose;
const enhancer = composeEnhancers(applyMiddleware(thunk));

// const store = createStore(allReducers, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(applyMiddleware(middlewareSatu)));
const store = createStore(allReducers, enhancer);

const App = () => {

  return (
    <Provider store={store}>
    <HashRouter>
   

   {/*  
    */}
      <Nav /> 
      <div className="main-content" id="panel">
        <Navbar />
        <Header />
        {/* 
        */}

        <div className="container-fluid mt--6">
   

    <Switch>
    <Route exact path="/" component={Home} />
  {/*
    <Route exact path="/dashboard" component={Dashboard} />
    <Route exact path="/dataanggota" component={DataAnggota} />
  */}
    <Route exact path="/kb" component={KecerdasanBuatan} />
    <Route exact path="/md" component={MatematikaDiskrit} />
    <Route exact path="/web" component={Web} />
    <Route exact path="/profile" component={Profile} />
    
    </Switch>

        </div>
      </div>
    
    </HashRouter>
    </Provider>
  )
}

ReactDOM.render(<App />, document.getElementById('root'));
